html, body {
    height: 100%;
    overflow: auto;
	margin: 0px;
	padding: 0px;
}

#shell {
    width: @contentAreaWidth;
	height: @contentAreaHeight;

    overflow: hidden;
}

#header {
	width: 100%;
	height: @headerHeight;
	line-height: @headerHeight;
	text-align: center;

	img {
		vertical-align: middle;
	}
}

#instructions {
	display: inline-block;
	height: @headerHeight - 10px;
	margin: 5px;
	padding: 5px 30px;
	width: calc(~"100% - 320px");
	background-color: @accentColor2;
	line-height: @headerHeight - 20px;
	color: white;
	font-weight: bold;
	box-sizing: border-box;
	position: relative;
	text-align: center;

	span {
		line-height: normal;
		display: inline-block;
		vertical-align: middle;
	    text-align: center;
	}

	.hover-button {
		position: absolute;
		right: -45px;
		top: -10px;
		display: none;
	}
}

@media screen and (max-width: 1000px) {
	#instructions {
		width: calc(~"100% - 40px");
		padding-left: 0px;
        font-size: 0.75em;

        .hover-button {
            top: -12px;
        }
	}

	#header {
		text-align: left;
	}
}

#status-bar {
	width: 100%;
	height: @statusBarHeight;
	line-height: 22px;
}

#content-container {
	position: relative;
	overflow: hidden;

	width: 100%;
	// ~" - " keeps less from evaluating the calculation on build.
	height: calc(@contentAreaHeight~" - "@headerHeight);
}

#contents {
	width: 100%;
	height: 100%;

	border: 0px;
    z-index: -1;
}

#nav-bar {
	width: 100%;
	height: @navBarHeight;

	padding-top: @smallMargin;
	white-space: nowrap;
}

#tray {
	width: 100%;
	height: @trayHeight;
}

@import "helpers";
@import "dialog";
@import "button";